<script>
import eduApi from "@/ajax/eduApi.js";
export default {
  data() {
    return {
      mobile: false,
      phone: "",
      errText: "",
      password: "",
    };
  },
  methods: {
    submit() {
      if (this.phone.length !== 11) {
        this.errText = "请输入11位的手机号";
        return;
      }
      const regPhone = /^1[\d]{10}$/g;
      if (!regPhone.test(+this.phone)) {
        this.errText = "请输入正确的手机号";
        return;
      }
      if (this.password.length < 8) {
        this.errText = "密码少于8位";
      } else if (this.password.length > 16) {
        this.errText = "密码大于16位";
      } else if (/[^._~!@#$^&*a-z0-9A-Z]/.test(this.password)) {
        this.errText = "密码包含非法字符";
      }
      if (this.errText.length > 0) {
        this.errText = "";
      }
      eduApi
        .login({
          userName: this.phone,
          password: this.password,
        })
        .then((req) => {
          this.$store.commit("setUseName", req.data.data);
          this.$router.push({ name: "home" });
        })
        .catch((err) => {
          alert(err.data.msg);
        });
    },
  },
};
</script>
